<template>
  <div>
    <h1></h1>
    <!--  -->
    <el-table :data="items">
      <!-- items._id -->
      <el-table-column prop="id" label="ID"></el-table-column>

      <el-table-column prop="content" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ timeChange(scope.row.time) }}
        </template>
      </el-table-column>

      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <!-- scope.row  -->
          <el-button
            type="text"
            size="small"
            @click="$router.push(`/teacher_watch/${scope.row.id}`)"
          >
            
          </el-button>
          <el-button type="text" size="small" @click="remove(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {
    // async fetchExample() {
    //   const res = await this.$http.get(`/display_examples`);
    //   // console.log(res.data.data);
    //   this.items = res.data.data;
    // },
    async fetchData() {
      const res = await this.$http.get(
        `http://server.versewow.cn/yunzhi/user/teacher_question?uuid=${localStorage.uuid}`
      );
      this.items = res.data.data;
      // console.log("", res);
    },
    async remove(row) {
      console.log(row.id);
      this.$confirm(`"${row.introduction}"`, "", {
        confirmButtonText: "ok",
        cancelButtonText: "cancel",
        type: "warning",
      }).then(async () => {
        const res = await this.$http.get(`/delete_example?exampleId=${row.id}`);
        this.$message({
          type: "success",
          message: "",
        });
        this.fetchExample();
      });
    },
    timeChange(value) {
      return this.$moment(value).format("YYYY-MM-DD HH:mm");
    },
  },
  created() {
    // this.fetchExample();
    this.fetchData();
  },
};
</script>
